#about {
    width: 100%;
    height: 400px;
    border-bottom: 2px solid #222;
    padding: 25px;
    padding-left: 10px;
    overflow-x: none;
    overflow-y: auto;
}
#AboutLeft {
    width: 40%;
    float: left;
}
#AboutRight {
    width: 60%;
    float: left;
    padding: 10px 0 0 25px;
    border-left: 1px solid #999;
    text-align: left;
    font-size: 1.5em;
    font-weight: 600;
}
#AboutPic {
    width: 75%;
    max-width: 320px;
    border-radius: 50%;
    border: 2px solid #333;
    box-shadow: 3px 3px 5px #555;
    margin-top: 25px;
}