.Nav {
  width: 100%;
  height: 50px;
  padding-top: 5px;
  background: #555;
  border-top: 2px solid #222;
  border-bottom: 2px solid #222;
}
.Nav > a {
  font-size: 1.5em;
  color: #fff;
  text-decoration: none;
  margin: 0 13px;
}
#themeBtn {
  min-width: 140px;
  padding: 5px 13px;
  border-radius: 8px;
  border: 2px solid #333;
  font-weight: 700;
  font-size: 1.2em;
  cursor: pointer;
}